<template>
  <el-container style="background-color:rgba(209, 233, 255, 0.2);height: 100%;">
    <el-header class="header">
      <el-row type="flex" justify="end" align="middle">
        <el-col :span="22">
          <el-menu :default-active="activeIndex" mode="horizontal" @select='handleSelect'>
            <el-menu-item index="图片">图片</el-menu-item>
            <el-submenu index="AI绘画教程">
              <template slot="title">AI绘画教程</template>
              <el-menu-item v-for="aiCourse in AiCourseList" :index="':'+aiCourse.id" :key="aiCourse.id">{{ aiCourse.name }}</el-menu-item>
            </el-submenu>
            <el-menu-item index="编程教程">编程教程</el-menu-item>
            <el-menu-item index="关于">关于</el-menu-item>
          </el-menu>
        </el-col>
        <el-col :span="2">
          <div style="width: 100%;">
            <el-button style="float:right" type="primary" >登录</el-button>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-main style="margin: 0px 30px;padding: 5px;height:100%;overflow: hidden;" >
      <div v-if="activeIndex == '图片'" style="height:100%;">
        <div class="scroll" style="height:100%;overflow-y: scroll;" @scroll="onScroll($event)">
          <el-row type="flex" justify="center" >
            <div class="image_list_list">
              <div class="image_list" v-for=" imgs in imgListList" :key="imgs.name" >
                <div class="image_item" v-for="img in imgs.list" :key="img.name">
                  <el-row type="flex" justify="center">
                    <el-image
                      style="border-radius: 3px;"
                      :src="img.url"
                      fit="scale-down"
                      :preview-src-list="imgss"
                      lazy
                      >
                      <div slot="error" class="image-slot">
                        <i class="el-icon-picture-outline"></i>
                      </div>
                      <div slot="placeholder" class="image-slot">
                        加载中<span class="dot">...</span>
                      </div>
                    </el-image>
                    <div class="image-download" @click="download(img.url)" v-bind:title="downloadTitle">
                      <i class="el-icon-download"></i>
                    </div>
                  </el-row>
                </div>
              </div>
            </div>
          </el-row>
          <el-row style="margin-top: 50px;" ><div style="cursor: pointer;"  @click="getPicture(true)"><el-divider>加载更多</el-divider></div></el-row>
        </div>
      </div>
      <div v-if="activeIndex.indexOf(':') != -1" style="height:100%;">
        <div v-for="aiCourse in AiCourseList" :index="aiCourse.id" :key="aiCourse.id" style="height:100%;position: absolute;">
          <div v-if="activeIndex == ':'+aiCourse.id" style="height:100%;overflow:auto">
            <div class="markdown-body" v-html="aiCourse.url"></div>
            <div style="background-color: white;" v-if="isComment">
              <div class="markdown-body">
                <h2 style="font-size: 30px;font-weight: bold;margin-top: 0px;">评论</h2>
                <div class="fontFamliy " style=" padding: 0px 120px 0px 50px;margin-bottom: 15px;" v-for="comment in commentList" :key="comment.id">
                  <div style="font-size: 20px;font-weight: 500">{{ comment.name }}</div>
                  <div style="padding-left: 30px;margin-top: 20px;font-size: 16px;text-indent:2em;font-weight: 400">{{ comment.content }}</div>
                  <div class="commentfontColor" style="padding-left: 30px;margin-top: 20px;font-size: 20px;" v-for="child in comment.childList" :key="child.id">
                    <p class="commentfontColor" style="font-size: 15px;text-indent:2em;margin: 0px;">{{ child.name }} : <span style="font-size: 15px;font-weight: 200;">{{ child.desc }}</span></p>
                  </div>
                  <el-divider></el-divider>
                </div>
                <h2 style="font-size: 30px;font-weight: bold;padding-top: 20px;margin-top: 0px;">发表评论</h2>
                <el-form ref="form" :model="form" label-width="60px" style="margin: 0 200px 0 100px;position: relative;">
                  <el-form-item label="名称:"><el-input placeholder="请输入名称" v-model="form.user_name" maxlength="10"></el-input></el-form-item>
                  <el-form-item label="邮箱:"><el-input placeholder="请输入邮箱" v-model="form.email" maxlength="30"></el-input></el-form-item>
                  <el-form-item label="评论:"><el-input placeholder="请输入内容" type="textarea" :rows="6" v-model="form.content"></el-input></el-form-item>
                  <el-button style="position: absolute;right: 10px;bottom: 10px;" type="primary" @click="onSubmit">提交</el-button>
              </el-form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="activeIndex == '编程教程'">
        <el-row type="flex" justify="center">编程教程</el-row>
      </div>
      <div v-if="activeIndex == '关于'">
        <el-row type="flex" justify="center">关于界面</el-row>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import { getPicturePageAndLimit, getPictureFile } from '../api/picture.js'
import { getAICourseArticle, getCodeCourseArticle } from '../api/article.js'
import { getCommentByArticleId, saveComment } from '../api/comment.js'
import 'github-markdown-css'
export default {
  data () {
    return {
      input: '',
      form: {
        user_name: '',
        email: '',
        content: ''
      },
      imgListList: [],
      imgss: [],
      downloadTitle: '下载',
      activeIndex: '图片',
      isloading: false,
      imgNum: 1,
      imgListNum: 3,
      page: 1,
      limit: 3,
      htmlMD: {},
      isComment: false,
      commentList: [],
      AiCourseList: [],
      codeCourseList: []
    }
  },
  methods: {
    onSubmit () {
      var tempForm = this.form
      if (tempForm.user_name !== '' && tempForm.email !== '' && tempForm.content !== '') {
        tempForm.parent_id = -1
        tempForm.article_id = this.activeIndex.substring(1)
        saveComment(tempForm).then(() => {
          // 更新评论
          this.form = this.$options.data().form
          getCommentByArticleId({article_id: this.activeIndex.substring(1)}).then((response) => {
            this.commentList = response.data
          })
        })
      }
    },
    onReset () {
      console.log('重置')
    },
    handleSelect (key, keyPath) {
      if (key.substring(0, 1) === ':') {
        this.isComment = true
        getCommentByArticleId({article_id: key.substring(1)}).then((response) => {
          this.commentList = response.data
        })
      } else {
        this.isComment = false
      }
      this.activeIndex = key
    },
    download (index) {
      getPictureFile(index).then((response) => {
        const blob = new Blob([response.data])
        const elink = document.createElement('a')
        elink.download = index.substring(index.lastIndexOf('/'))
        elink.style.display = 'none'
        elink.href = URL.createObjectURL(blob)
        document.body.appendChild(elink)
        elink.click()
        URL.revokeObjectURL(elink.href) // 释放URL 对象
        document.body.removeChild(elink)
      }).catch((error) => {
        this.$message({
          message: error
        })
      })
    },
    onScroll (event) {
      if ((event.target.scrollHeight - event.target.clientHeight) - 30 < event.target.scrollTop) {
        if (!this.isloading) {
          this.getPicture()
        }
      }
      if ((event.target.scrollHeight - event.target.clientHeight) * 0.6 > event.target.scrollTop) {
        this.isloading = false
      }
    },
    getPicture (isForce) {
      if (!this.isloading || isForce) {
        this.isloading = true
        return getPicturePageAndLimit({'page': this.page, 'limit': this.limit}).then((response) => {
          var data = response.data
          for (const iterator of data) {
            console.log(this.imgListNum)
            if (this.imgNum % this.imgListNum !== 0) {
              iterator.name = this.imgNum
              this.imgListList[(this.imgNum % this.imgListNum) - 1].list.push(iterator)
            } else {
              iterator.name = this.imgNum
              this.imgListList[this.imgListNum - 1].list.push(iterator)
            }
            this.imgss.push(iterator.url)
            this.imgNum++
          }
          this.isloading = false
          this.page++
          console.log(this.imgListList)
        })
      }
    }
  },
  created () {
    for (let index = 0; index < this.imgListNum; index++) {
      this.imgListList.push({name: '/' + index, list: []})
    }
  },
  mounted () {
    // this.imgListList[0].list.push({name: '1', url: require('../img/1.png')})
    // this.imgListList[0].list.push({name: '2', url: require('../img/2.png')})
    // this.imgListList[0].list.push({name: '3', url: require('../img/3.png')})
    // this.imgListList[1].list.push({name: '4', url: require('../img/4.png')})
    // this.imgListList[1].list.push({name: '5', url: require('../img/5.png')})
    // this.imgListList[1].list.push({name: '6', url: require('../img/6.png')})
    // this.imgListList[2].list.push({name: '7', url: require('../img/7.png')})
    // this.imgListList[2].list.push({name: '8', url: require('../img/8.png')})
    // this.imgListList[2].list.push({name: '9', url: require('../img/9.png')})

    // this.imgss.push(require('../img/1.png'))
    // this.imgss.push(require('../img/2.png'))
    // this.imgss.push(require('../img/3.png'))
    // this.imgss.push(require('../img/4.png'))
    // this.imgss.push(require('../img/5.png'))
    // this.imgss.push(require('../img/6.png'))
    // this.imgss.push(require('../img/7.png'))
    // this.imgss.push(require('../img/8.png'))
    // this.imgss.push(require('../img/9.png'))

    this.getPicture().catch(() => {

    }).finally(() => {
      this.getPicture().finally(() => {
        this.getPicture()
      })
    })
    this.AiCourseList = [{id: '1', name: 'Stable Diffusion AI教程', url: require(`../img/stable-diffucsion1.md`)}, {id: '2', name: 'Stable Diffusion AI教程', url: ''}]
    this.codeCourseList = [{id: '3', name: '编程教程1', url: require(`../img/stable-diffucsion1.md`)}, {id: '4', name: '编程教程2', url: require(`../img/stable-diffucsion1.md`)}]
    getAICourseArticle().then((response) => {
      this.AiCourseList = response.data
    })
    getCodeCourseArticle().then((response) => {
      this.codeCourseList = response.data
    })
    this.commentList.push({id: 1, name: '龙羊峡中扬', content: 'Stable Diffusion 支持txt2img(文生图)和img2img（图生图）。一般用文生成图。想要生成比较好看的图像还是不容易的，需要选到好的模型和对应的prompt。需要多多学习和实验，最终成为魔法咒语大师(笑)。', childList: [{id: 7, name: '飞天小堇瓜', desc: '都是骗人的，别信', childList: []}, {id: 8, name: '红豆糯米团er', desc: '确实', childList: []}]})
    this.commentList.push({id: 2, name: '飞天咸鱼', content: 'Stable Diffusion 是根据模型生成的，不同的模型对同一prompt生成的图片风格和内容都不一样。有一些针对真人的，有一些是生成二次元的，有一些是专门生成se图的。', childList: [{id: 3, name: '三猫一汪的日常', desc: '什么？', childList: []}]})
    this.commentList.push({id: 4, name: '跳跳茄', content: '针对真人的(笑)。', childList: []})
  }
}
</script>

<style scoped>

.toolbox{
  display: inline-block;
  display: inline-flex;
  justify-content: center;
}
.header_pBtn {
  display: inline-block;
  padding: 5px 15px;
  margin: 10px;
  border-radius: 15px;
  font-size:medium;
  cursor:pointer;
  height: fit-content;
  width: fit-content;
}
.image_list_list{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  justify-content: flex-start;
  height: 100%;
  width: 100%;
}
.image_list{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  width: 33%;
  height:100%;
  margin: 0.1%;
}
.image_item{
  height: 100%;
  width: 100%;
  margin: 0.3%;
}
.header_pBtn:hover{
  background:rgba(180, 203, 219, 0.232);
}
.image-slot{
  height: 100%;
  width: 100%;
}
.image-download{
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
  border-radius: 2px;
  width: 15px;
  height: 15px;
  position: absolute;
  right: 2%;
  bottom: 2%;
  background-color: rgba(210, 220, 229, 0.412);
}
.image-download:hover{
  background-color: aliceblue;
}
.header{
  padding: 0px 50px;
  background-color:rgba(255, 255, 255, 1);
  border-bottom: solid 1px #5a606e33;
}
.fontFamliy{
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Noto Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji";
}
.commentfontColor{
  color: #1a2125 !important;
}
</style>



